.whatwedo {
  /* background: url("../../images/aboutus-bg.jpg"); */
  /* background-attachment: fixed; */
  padding: 45px 0;
  margin-top: 7rem;
}

.fadeinleft {
  animation: fadeinleft 0.9s both;
}

@keyframes fadeinleft {
  0% {
    opacity: 0;
    transform: translatey(300px);
  }
  100% {
    opacity: 1;
    transform: translatey(0);
  }
}

.whatwedo .text {
  color: #828282;
  font-size: 1.3rem;
  line-height: 40px;
  display: flex;
  justify-content: center;
  padding: 25px;
  font-family: "Roboto";
}

